import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, LotacaoTributaria } from '../../../../models/dtos/mfp/lotacaoTributaria';
import { TIPO_LOTACAO_TRIBUTARIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoLotacaoTributariaEnum';

interface LotacaoTributariaEditViewProps extends EditViewProps<LotacaoTributaria> {}

const LotacaoTributariaEditView: FC<LotacaoTributariaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<LotacaoTributaria>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de Lotação Tributária" wikiId={527}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="tipoLotacaoTributaria"
              inputDropDownProps={{ optionsProvider: TIPO_LOTACAO_TRIBUTARIA_ENUM_VALUES, selectProps: { autoFocus: true } }}
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Tipo Lotação Tributária"
              placeholder="Informe o Tipo da Lotação Tributária"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 255, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição"
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default LotacaoTributariaEditView;
